
.i-audio {
  width: 40px;
  height: 40px;
  background: url(./assets/music.png) no-repeat;
  background-size: contain;
}
.i-audio.on {
    animation: musicAnimation 2s infinite linear;
}
@keyframes musicAnimation {
0% {
    transform: rotate(0);
}
100% {
    transform: rotate(360deg);
}
}



