
*,
:after,
:before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
html {
  background: #eee;
}
body {
  font-size: 14px;
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
}
a {
  -webkit-text-decoration-skip: objects;
  color: #42b983;
  background: 0 0;
  cursor: pointer;
  transition: color 0.2s ease;
  text-decoration: none;
}
a:active,
a:hover {
  outline: 0;
  text-decoration: none;
}
ol,
ul {
  list-style: none;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.wrapper {
  background: #eee;
  padding: 1px;
}
.wrapper .logo {
  margin: 10px auto;
  height: 80px;
  text-align: center;
}
.wrapper .logo img {
  width: 80px;
  transition: all 0.7s cubic-bezier(0, 0.51, 0.69, 1.15);
  transform: rotate(0) scale(10);
  transform-origin: center center;
  opacity: 0;
}
.wrapper .logo img.load {
  transform: rotate(1080deg) scale(1);
  opacity: 1;
}
.wrapper .ui-name {
  font-size: 2.2rem;
  text-align: center;
}
.wrapper .ui-name i {
  font-style: normal;
}
.wrapper .ui-name i:nth-child(1) {
  color: #f06;
}
.wrapper .ui-name i:nth-child(2) {
  color: #0089ce;
}
.wrapper .ui-name i:nth-child(3) {
  color: #f1d800;
}
.wrapper .ui-name i:nth-child(4) {
  color: #00d39a;
}
.wrapper .ui-name i:nth-child(5) {
  color: #94e827;
}
.wrapper .wrapper-container {
  background: #fcfcfc;
  width: 90%;
  margin: 10px auto 20px;
  border-radius: 6px;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,0.1);
  overflow: hidden;
}
.wrapper .wrapper-content {
  padding: 20px;
}
.wrapper .wrapper-content .demo-link {
  font-size: 1rem;
  color: #42b983;
}
.wrapper .wrapper-content .demo-link:hover {
  text-decoration: underline;
}
.wrapper .wrapper-content table {
  margin-bottom: 30px;
}
.wrapper .wrapper-content code {
  width: 100%;
  margin-bottom: 20px;
  background: #e1e1e1;
  font-size: 1rem;
  transition: all 0.5s ease;
}
.wrapper .wrapper-content code:hover {
  box-shadow: 0 6px 10px 0 rgba(0,0,0,0.14), 0 1px 18px 0 rgba(0,0,0,0.12), 0 3px 5px -1px rgba(0,0,0,0.3);
}
@media (max-width: 600px) {
.wrapper .logo {
    margin: 10px 0;
    height: 40px;
}
.wrapper .logo img {
    width: 40px;
}
.wrapper .ui-name {
    font-size: 1.7rem;
}
.wrapper .wrapper-container {
    width: 98%;
    margin: 15px 0 10px;
}
.wrapper .wrapper-navigate li {
    text-align: center;
}
.wrapper .wrapper-navigate li div {
    font-size: 13.5px;
    text-align: left;
}
.wrapper .wrapper-navigate li a {
    padding-left: 5px;
    font-size: 12px;
}
.wrapper .wrapper-navigate li a .icon {
    margin: auto;
    width: 20px;
}
.wrapper .wrapper-navigate li a span {
    display: none;
}
}


